import React, { Component } from 'react'
import './App.css'
import "./styles/menu.css";
import { Player } from "./components";
import { Routes } from './pages'

class App extends Component {

  constructor(props) {
    super(props);
    this.state={
      jsonData : ""
    }
    this.audio = "";
  }

  componentWillMount() {
    fetch("https://theaudiodb.p.rapidapi.com/searchtrack.php?s=one%20direction&t=drag%20me%20down", {
            "method": "GET",
            "headers": {
                "x-rapidapi-key": "f283298096msh637c19f200dd590p1d1327jsn0cc56e94757",//2,
                "x-rapidapi-host": "theaudiodb.p.rapidapi.com"
                },
            "Content-Type": "application/json",
            "mode": "cors"
        })
        .then(response => {
            response.json()

                .then( json => {
                    
                    //this.url = json.track[0].strMusicVid;
                    
                    this.audio = new Audio(this.url);
                    console.log(json);
                    this.setState({jsonData: json});
                })
                .catch(err => {
                    console.error(err);
                });
            })
        .catch(err => {
            console.error(err);
        });
  }

    render() {
        return (
            <div className="App">
                <Routes globalState={this.state.jsonData}/>
                <Player></Player>
            </div>
        )}
}

export default App;
